<template>
  <div>
    <el-row :gutter=20>
      <el-col :span="4">
        <club_aside></club_aside>
      </el-col>
      <el-col :span=20 v-if="Authenticate===true">
        <el-form :model="PostForm" :rules="rules" class="" labelPosition="right">
          <el-form-item label="" :required=true>
            <el-col :span=4>
              <el-form-item :prop="LinkmanGrade">
                <el-select v-model="PostForm.LinkmanGrade" placeholder="" value="">
                  <el-option label="" value="1"></el-option>
                  <el-option label="" value="2"></el-option>
                  <el-option label="" value="3"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span=4>
              <el-form-item :prop="LinkmanClass">
                <el-select v-model="PostForm.LinkmanClass" placeholder="" value="">
                  <el-option label="1" value="1"></el-option>
                  <el-option label="2" value="2"></el-option>
                  <el-option label="3" value="3"></el-option>
                  <el-option label="4" value="4"></el-option>
                  <el-option label="5" value="5"></el-option>
                  <el-option label="6" value="6"></el-option>
                  <el-option label="7" value="7"></el-option>
                  <el-option label="8" value="8"></el-option>
                  <el-option label="9" value="9"></el-option>
                  <el-option label="10" value="10"></el-option>
                  <el-option label="11" value="11"></el-option>
                  <el-option label="12" value="12"></el-option>
                  <el-option label="13" value="13"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span=4>
              <el-form-item :prop="LinkmanName">
                <el-input v-model="PostForm.LinkmanName" placeholder=""></el-input>
              </el-form-item>
            </el-col>
            <el-col :span=4>
              <el-form-item :prop="LinkmanPhoneNumber" :required=true>
                <el-input v-model="PostForm.LinkmanPhoneNumber" placeholder=""></el-input>
              </el-form-item>
            </el-col>
            <el-col :span=4>
              <el-form-item :prop="LinkmanQq" :required=true>
                <el-input v-model="PostForm.LinkmanQq" placeholder="QQ"></el-input>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-col :span=20>
            <el-form-item label="" :prop="Region" :required=true>
              <el-input v-model="PostForm.Region"></el-input>
            </el-form-item>
          </el-col>
          <el-form-item label="" :required=true>
            <el-form-item :prop="Date1">
              <el-date-picker
                v-model="PostForm.Date1"
                type="datetime"
                placeholder="">
              </el-date-picker>
            </el-form-item>
          </el-form-item>
          <el-form-item label="" :prop="Content" :required=true>
            <el-input type="textarea" v-model="PostForm.Content"></el-input>
          </el-form-item>
          <el-form-item label="" :prop="Process" :required=true>
            <el-input type="textarea" v-model="PostForm.Process"></el-input>
          </el-form-item>
          <el-form-item label="" :prop="Assessment" :required=true>
            <el-input type="textarea" v-model="PostForm.Assessment"></el-input>
          </el-form-item>
          <el-form-item label="" :prop="Feeling" :required=true>
            <el-input type="textarea" v-model="PostForm.Feeling"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm"></el-button>
          </el-form-item>
        </el-form>
        <el-row>
          <el-col span="6" offset="18">
            <a v-if="Settings.message==='Error'"></a>
            <a v-if="Settings.message==='Success'"></a>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span=20 v-if="Authenticate===false || Authenticate===null">
        <p></p>
      </el-col>
    </el-row>
  </div>
</template>
<script>
  import axios from 'axios'
  import JAside from '../../components/admin_club/ClubAside.vue'
  export default {
    components: {
      'club_aside': JAside
    },
    data () {
      return {
        PostForm: {
          ClubName: this.GetClubName,
          LinkmanGrade: '',
          LinkmanClass: '',
          LinkmanName: '',
          LinkmanPhoneNumber: '',
          LinkmaanQq: '',
          Region: '',
          Date1: '',
          Content: '',
          Process: '',
          Assessment: '',
          Feeling: ''
        },
        Settings: {
          message: ''
        },
        rules: {
          ClubName: [
            {required: true, message: '', trigger: 'blur'}
          ],
          Region: [
            {required: true, message: '', trigger: 'blur'}
          ],
          Date1: [
            {type: 'date', required: true, message: '', trigger: 'change'}
          ],
          Process: [
            {required: true, message: '', trigger: 'change'}
          ],
          Assessment: [
            {required: true, message: '', trigger: 'change'}
          ],
          Content: [
            {required: true, message: '', trigger: 'change'}
          ],
          Feeling: [
            {min: 200, required: true, message: '', trigger: 'change'}]
        }
      }
    },
    methods: {
      submitForm () {
        axios({
          method: 'POST',
          url: '/api/club/post/EditSubmit',
          data: JSON.stringify({
            ClubId: this.GetClubId,
            ClubName: this.GetClubName,
            Linkman: this.PostForm.Linkman,
            Region: this.PostForm.Region,
            Date1: this.PostForm.Date1,
            Content: this.PostForm.Content,
            Process: this.PostForm.Process,
            Assessment: this.PostForm.Assessment,
            Feeling: this.PostForm.Feeling,
            Token: this.GetToken
          })
        }).then(function (response) {
          if (response.data.message === 'Error') {
            this.Settings.message = 'Error'
          } else if (response.data.message === 'Success') {
            this.Settings.message = 'Success'
            this.resetForm('PostForm')
          }
        }).catch(function () {
          alert('error: PostEdit')
        })
      },
      resetForm (formName) {
        this.$refs[formName].resetFields()
      }
    },
    computed: {
      Authenticate () {
        return this.$store.state.Authenticated
      },
      GetClubName () {
        return this.$store.state.UserName
      },
      GetClubId () {
        return this.$store.state.ClubId
      },
      GetToken () {
        return this.$store.state.Token
      }
    }
  }
</script>
<style>
</style>



